.main-background {
  width: 100%;
  background-color: rgba(119, 136,153, 45%);
}
/* component sizing and background */
.background {
  width: 375px;
  background-color: rgba(119, 136,153, 50%);
  font-family: "Open Sans", sans-serif;
  font-weight: 300;
  padding: 10px;
}
/* logo */
.logo {
  border-radius: 100%;
  height: 200px;
  width: 200px;
  margin: 10px;
  box-shadow: 2px 2px 5px black;
}
/* education institution images */
.ed {
  width: 150px;
  padding-top: 20px;
}
/* container elements */
.section-container {
  background-color: lightslategray;
  color: steelblue;
  border: none;
  border-radius: 15px;
  box-shadow: 2px 2px 5px black;
  margin: 10px;
  padding: 10px;
}
/* container element for education images */
.ed-container {
  background-color: steelblue;
  border-radius: 100%;
  box-shadow: 2px 2px 5px black;
  margin: 10px;
  padding: 10px;
  width: 200px;
  height: 200px;
}
/* container element for education text */
.ed-text-container {
  background-color: lightgray;
  border-radius: 15px;
  border: none;
  box-shadow: 2px 2px 5px black;
  margin: 10px;
  padding: 10px;
  width: 315px;
}
/* main titles */
h1 {
  color: navy;
  border: none;
  border-radius: 15px;
  font-family: "Architects Daughter", cursive;
  font-weight: 300;
  font-size: 42px;
  margin: 10px;
  padding: 10px;
}
/* section titles */
h2 {
  background-color: none;
  color: navy;
  border: none;
  border-radius: 15px;
  font-family: "Architects Daughter", cursive;
  font-weight: 300;
  font-size: 32px;
  margin: 10px 0 10px 0;
  padding: 10px;
  text-align: left;
}
/* education titles */
h3 {
  background-color: none;
  color: navy;
  border: none;
  font-size: 28px;
  margin: 10px;
  padding: 0;
}
/* education subtitles */
h4 {
  background-color: none;
  color: navy;
  border: none;
  font-size: 22px;
  margin: 10px;
  padding: 0;
}
/* section text */
.section-text {
  background-color: none;
  color: lightgray;
  border: none;
  font-size: 16px;
  margin: 10px;
  text-align: left; 
}
/* education text */
.ed-text {
  color: navy;
  margin: 10px;
  padding: 0;
  text-align: center;
}
/* buttons */
.button {
  background-color: steelblue;
  color: navy;
  border: none;
  border-radius: 40px;
  box-shadow: 2px 2px 5px black;
  font-family: "Architects Daughter", cursive;
  font-weight: 300;
  font-size: 21px;
  outline: none;
  margin: 10px;
  padding: 5px;
  width: 100px;
}
.button:hover, .button:focus {
  cursor: pointer;
  opacity: 0.7;
  border: none;
  outline: none;
  color: whitesmoke;
}
/* square buttons */
.square {
  color: whitesmoke;
  border: none;
  border-radius: 10px;
  box-shadow: 2px 2px 5px black;
  margin: 10px;
  padding-top: 10px;
  outline: none;
  cursor: pointer;
  text-decoration: none;
  font-size: 24px;
  height: 2.25em;
  width: 2.25em;
}
.square:hover, .square:focus {
  color: white;
  text-decoration: none;
  opacity: 0.7;
  border: none;
  outline: none;
}
.linkedin {
  background-color: #0077B5;
}
.github {
  background-color: #24292e;
}
.resume {
  background-color: orangered;
}
/* project display elements */
select:enabled, select:active, select:focus {
  background-color: lightgray;
  color: navy;
  border: none;
  border-radius: 15px;
  box-shadow: 2px 2px 5px black;
  font-size: 18px;
  margin: 10px;
  padding: 10px;
  width: 315px;
}
.project-img {
  width: 315px;
  box-shadow: 2px 2px 5px black;
  border-radius: 15px;
  margin: 10px;
}
.link-container {
  background-color: lightgray;
  color: navy;
  border: none;
  border-radius: 15px;
  box-shadow: 2px 2px 5px black;
  margin: 10px 10px 20px 10px;
  padding: 10px;  
  text-align: center;
  width: 315px;
}
.project-link {
  color: navy;
  font-size: 18px;
  font-family: "Open Sans", sans-serif;
  font-weight: 300;
  padding: 10px;
}
.project-text {
  background-color: steelblue;
  color: lightgray;
  border: none;
  border-radius: 15px;
  box-shadow: 2px 2px 5px black;
  margin: 10px;
  padding: 10px;
  text-align: center;
}
/* email and phone contact information formatting */
.contact-info {
  background-color: lightgray;
  color: navy;
  border: none;
  border-radius: 15px;
  box-shadow: 2px 2px 5px black;
  font-size: 22px;
  text-align: center;
  margin: 10px !important;
  padding: 10px !important;
  width: 315px !important;
}
/* contact form */
label {
  background-color: none;
  color: navy;
  border: none;
  font-weight: 300;
  font-size: 21px;
  margin: 10px;
  padding: 0;
  text-align: left !important;
  width: 315px;
}
input:enabled, input:active, input:focus {
  background-color: whitesmoke;
  color: navy;
  box-shadow: 2px 2px 5px black;
  border: none;
  border-radius: 15px;
  font-family: "Architects Daughter", cursive;
  font-weight: 300;
  font-size: 18px;
  margin: 0 10px 10px 10px;
  padding: 5px 5px 5px 10px;
  text-align: left !important;
  width: 315px;
}
textarea:enabled, textarea:active, textarea:focus {
  background-color: whitesmoke;
  color: navy;
  box-shadow: 2px 2px 5px black;
  border: none;
  border-radius: 15px;
  font-family: "Architects Daughter", cursive;
  font-weight: 300;
  font-size: 22px;
  margin: 0 10px 0 10px;
  padding: 5px 5px 5px 10px;
  text-align: left !important;
  width: 315px;
}
/* show and hide classes used in script.js */
.show {
    display: block;
}
.hide {
    display: none;
}

.footer {
  background-color: navy;
  color: white;
}